import React, { Component, Fragment } from 'react';
import Logo from '../components/Header/Logo/Logo';
// import Navigation from '../components/Header/Navigation/Navigation';
import TrendingMovies from '../components/Main/TrendingMovies/TrendingMovies';

import styles from '../components/Header/Navigation/Navigation.module.css';
import stylesQueue from '../components/Main/TrendingMovies/TrendingMovies.module.css';

// import { object } from 'prop-types';

export default class Queue extends Component {
    state = {
        // width: window.innerWidth,
        localStorageQueueArr: [],
        arrMov: [],
        configuration: {},
    };

    // updSize = () => {
    //     this.setState({
    //         width: window.innerWidth,
    //     });
    // };

    componentDidMount() {
        // window.addEventListener('resize', this.updSize);
        let tasks = localStorage.getItem('queue');
        if (tasks) {
            this.setState({
                arrMov: JSON.parse(tasks),
            });
        }
        let config = this.props.location.state.configuration;
        this.setState({ configuration: config });
    }

    // componentWillUnmount() {
    //     window.removeEventListener('resize', this.updSize);
    // }

    render() {
        let { arrMov, configuration } = this.state;
        return (
            <Fragment>
                <header>
                    <div className={styles.wrapHeader}>
                        <Logo />
                        {/* <Navigation onWidth={width} /> */}
                    </div>
                </header>
                <main>
                    <div className={stylesQueue.wrapQueueWathcedMain}>
                        {arrMov.length !== 0 && (
                            <TrendingMovies
                                location={this.props.location.pathname}
                                arrMovies={arrMov}
                                configuration={configuration}
                            />
                        )}
                    </div>
                </main>
            </Fragment>
        );
    }
}
